.btn {
    background: rgb(247, 150, 192);
    background: radial-gradient(circle, rgba(247, 150, 192, 1) 0%, rgba(118, 174, 241, 1) 100%);
    line-height: 42px;
    padding: 0;
    border: 1px solid rgb(100, 149, 208);
    color: #ffffff;
}

button:disabled {
    background: rgb(143, 155, 205);
}

button:disabled:hover {
    background: rgb(143, 155, 205);
    box-shadow: none;
    border: none;
}

.btn span {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
}

.btn:before,
.btn:after {
    position: absolute;
    content: "";
    height: 0%;
    width: 1px;
    box-shadow: -1px -1px 20px 0px rgba(255, 255, 255, 1), -4px -4px 5px 0px rgba(255, 255, 255, 1), 7px 7px 20px 0px rgba(0, 0, 0, 0.5), 4px 4px 5px 0px rgba(0, 0, 0, .3);
}

.btn:before {
    right: 0;
    top: 0;
    transition: all 500ms ease;
}

.btn:after {
    left: 0;
    bottom: 0;
    transition: all 500ms ease;
}

.btn:hover {
    background: rgb(230, 240, 252);
    color: #00089b;
    box-shadow: -1px -1px 10px 0px rgba(255, 255, 255, 0.7), -4px -4px 5px 0px rgba(255, 255, 255, 0.7), 7px 7px 10px 0px rgba(0, 0, 0, 0.5), 4px 4px 5px 0px rgba(0, 0, 0, .3);
    border: none;
}

.btn:hover:before {
    transition: all 500ms ease;
    height: 100%;
}

.btn:hover:after {
    transition: all 500ms ease;
    height: 100%;
}

.btn span:before,
.btn span:after {
    position: absolute;
    content: "";
    box-shadow: -1px -1px 20px 0px rgba(255, 255, 255, 1), -4px -4px 5px 0px rgba(255, 255, 255, 1), 7px 7px 20px 0px rgba(0, 0, 0, .4), 4px 4px 5px 0px rgba(0, 0, 0, .3);
}

.btn span:before {
    left: 0;
    top: 0;
    width: 0%;
    height: .5px;
    transition: all 500ms ease;
}

.btn span:after {
    right: 0;
    bottom: 0;
    width: 0%;
    height: .5px;
    transition: all 500ms ease;
}

.btn span:hover:before {
    width: 100%;
}

.btn span:hover:after {
    width: 100%;
}