.btn {
  color: #fff;
  background: radial-gradient(circle, #f796c0 0%, #76aef1 100%);
  border: 1px solid #6495d0;
  padding: 0;
  line-height: 42px;
}

button:disabled {
  background: #8f9bcd;
}

button:disabled:hover {
  box-shadow: none;
  background: #8f9bcd;
  border: none;
}

.btn span {
  width: 100%;
  height: 100%;
  display: block;
  position: relative;
}

.btn:before, .btn:after {
  content: "";
  height: 0%;
  width: 1px;
  position: absolute;
  box-shadow: -1px -1px 20px #fff, -4px -4px 5px #fff, 7px 7px 20px rgba(0, 0, 0, .5), 4px 4px 5px rgba(0, 0, 0, .3);
}

.btn:before {
  transition: all .5s;
  top: 0;
  right: 0;
}

.btn:after {
  transition: all .5s;
  bottom: 0;
  left: 0;
}

.btn:hover {
  color: #00089b;
  background: #e6f0fc;
  border: none;
  box-shadow: -1px -1px 10px rgba(255, 255, 255, .7), -4px -4px 5px rgba(255, 255, 255, .7), 7px 7px 10px rgba(0, 0, 0, .5), 4px 4px 5px rgba(0, 0, 0, .3);
}

.btn:hover:before, .btn:hover:after {
  height: 100%;
  transition: all .5s;
}

.btn span:before, .btn span:after {
  content: "";
  position: absolute;
  box-shadow: -1px -1px 20px #fff, -4px -4px 5px #fff, 7px 7px 20px rgba(0, 0, 0, .4), 4px 4px 5px rgba(0, 0, 0, .3);
}

.btn span:before {
  width: 0%;
  height: .5px;
  transition: all .5s;
  top: 0;
  left: 0;
}

.btn span:after {
  width: 0%;
  height: .5px;
  transition: all .5s;
  bottom: 0;
  right: 0;
}

.btn span:hover:before, .btn span:hover:after {
  width: 100%;
}

